import { Box, Grid, GridItem, Heading } from "@chakra-ui/react"
import Menu from "../components/menu/menu"
import renderAst from "../utils/render-ast"
import Toc from "../components/toc/toc"
import Feedback from "../components/feedback/feedback"
import * as React from "react"
import { useEffect, useMemo, useState } from "react"
import { getTocData } from "../utils/get-toc-data"

const DocumentationPage = ({ data, location }) => {
  const [offset, setOffset] = useState(0)

  useEffect(() => {
    const onScroll = () => setOffset(window.pageYOffset)
    window.addEventListener("scroll", onScroll, { passive: true })
    return () => window.removeEventListener("scroll", onScroll)
  }, [])

  const tocData = useMemo(() => {
    if (data?.toc?.htmlAst == null) {
      console.warn(
        `Table of contents not defined for page ${location?.pathname}`
      )
      return []
    }

    try {
      return getTocData(data?.toc?.htmlAst)
    } catch (e) {
      console.error(`Table of contents failed for page ${location?.pathname}`)
    }
  }, [data])

  const page = data.pageContent

  return (
    <Grid
      py={20}
      templateColumns={{
        base: "1fr",
        lg: "30% 70%",
        xl: "1fr 42rem 1fr",
      }}
      gap={10}
    >
      <GridItem
        display={{ base: "none", md: "none", lg: "block", xl: "block" }}
        maxW={"20rem"}
        pl="1em"
        position="sticky"
        top={offset > 150 ? 104 : ""}
        maxH="70vh"
        overflow="auto"
      >
        <Menu content={tocData} rootTitle={data.toc} location={location} />
      </GridItem>

      <GridItem px="1em" maxW={{ lg: "42rem" }}>
        <article className="DocSearch-content">
          {renderAst(page.htmlAst)}
        </article>
      </GridItem>

      <GridItem
        maxW={"20rem"}
        display={{ base: "none", md: "none", lg: "none", xl: "block" }}
      >
        <Box position="sticky" top={offset > 150 ? 104 : ""} pr="1em">
          <Heading fontSize={20} fontWeight={700} pb={2.5}>
            IN THIS PAGE
          </Heading>
          <Box maxH="60vh" overflow="auto">
            <Toc data={data.pageContent} location={location} />
          </Box>
          <Feedback location={location} />
        </Box>
      </GridItem>
    </Grid>
  )
}

export default DocumentationPage
